<script>
import { queryDetailById } from '@/api/index';

export default {
  data() {
    return {
      data: {
        member: {},
        consumeRecord: {},
        rechargeRecord: {}
      }
    };
  },
  created() {
    queryDetailById(this.$route.query.id).then(res => {
      if (res) {
        if (res.data.code === 0) {
          this.data = res.data.data;
        } else {
          this.$Toast(res.data.msg);
        }
      }
    });
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    }
  }
};
</script>

